import { Component, OnInit, ViewChild, ElementRef, Renderer2 } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { QuizService } from '../dashboard/services/quiz.service';
import { Quiz } from '../dashboard/services/quiz';

@Component({
  selector: 'app-quiz-test',
  templateUrl: './quiz-test.component.html',
  styleUrls: ['./quiz-test.component.css'],
  providers: [QuizService]
})
export class QuizTestComponent implements OnInit {

  quizElement: Quiz;
  right: number;
  errorMessage: string = '';
  counter: number = 0;

  constructor(
    private renderer: Renderer2,
    private route: ActivatedRoute,
    private quizService: QuizService,
    private location: Location) { }

  ngOnInit() {
    var urlKey = this.route.snapshot.params['key'];
    this.getSingleQuiz(urlKey);
  }

  getSingleQuiz(urlKey: String) {
    var x = this.quizService.getData();
    x.snapshotChanges().subscribe(item => {
      item.forEach(element => {
        var y = element.payload.toJSON();
        y["key"] = element.key;
        if (element.key === urlKey) {
          this.quizElement = y as Quiz;
        }
      });
    });
  }

  checkAnswer(answer: String, x: number, quiz: Quiz) {
    if (answer === this.quizElement.rightAnswer) {
      this.right = x;
      if (this.counter > 0) {
        this.errorMessage = "This is the right answer! You have already answer this right.";
      } else {
        this.errorMessage = "Well done! You gain " + quiz.points + " points!";
        this.counter++;
      }
    } else {
      this.errorMessage = "You must read study more material level " + quiz.level + ". Do not give up!";
    }
  }
}
