import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';
import { AngularFireModule } from 'angularfire2';
import { AngularFirestoreModule } from 'angularfire2/firestore';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { AngularFireAuthModule } from 'angularfire2/auth';
import { FirebaseApp } from '@firebase/app-types';
import { FirebaseAuth } from '@firebase/auth-types';
import { FirebaseDatabase } from '@firebase/database-types';
import { FirebaseMessaging } from '@firebase/messaging-types';
import { FirebaseStorage } from '@firebase/storage-types';
import { AngularFireDatabase, AngularFireAction, AngularFireObject } from 'angularfire2/database';
import { FirebaseFirestore } from '@firebase/firestore-types';
import { environment } from '../environments/environment';

import { AppComponent } from './app.component';
import { NavbarComponent } from './navbar/navbar.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { QuizLevelsComponent } from './quiz-levels/quiz-levels.component';
import { MaterialTileComponent } from './quiz-levels/material-tile/material-tile.component';
import { QuizTileComponent } from './quiz-levels/quiz-tile/quiz-tile.component';
import { QuizTestComponent } from './quiz-test/quiz-test.component';
import { MaterialComponent } from './material/material.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { QuizFormComponent } from './dashboard/quiz-form/quiz-form.component';
import { QuizListComponent } from './dashboard/quiz-list/quiz-list.component';
import { MaterialFormComponent } from './dashboard/material-form/material-form.component';
import { MaterialListComponent } from './dashboard/material-list/material-list.component';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { AuthService } from './core/auth.service';
import { AuthGuard } from './core/auth.guard';
import { UserService } from './core/user.service';
import { InformationComponent } from './information/information.component';

const appRoutes: Routes = [
  { path: '', component: LoginComponent },
  { path: 'register', component: RegisterComponent},
  { path: 'quiz-levels', component: QuizLevelsComponent},
  { path: 'dashboard', component: DashboardComponent },
  { path: 'information', component: InformationComponent },
  { path: 'quiz-test/:key', component: QuizTestComponent},
  { path: 'material/:key', component: MaterialComponent}
];

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    DashboardComponent,
    QuizFormComponent,
    QuizListComponent,
    HeaderComponent,
    FooterComponent,
    QuizLevelsComponent,
    MaterialComponent,
    MaterialTileComponent,
    QuizTileComponent,
    MaterialListComponent,
    MaterialFormComponent,
    QuizTestComponent,
    LoginComponent,
    RegisterComponent,
    InformationComponent
  ],
  imports: [
    BrowserModule,
    AngularFireModule.initializeApp(environment.firebase),
    AngularFirestoreModule,
    AngularFireAuthModule,
    AngularFireDatabaseModule,
    AngularFireModule,
    NgbModule.forRoot(),
    ReactiveFormsModule,
    FormsModule,
    RouterModule.forRoot(
      appRoutes,
      { enableTracing: true }
    )
  ],
  providers: [AuthService],
  bootstrap: [AppComponent]
})
export class AppModule { }

